/* * @NOTE: Prepend a `~` to css file paths that are in your node_modules * See https://github.com/webpack-contrib/sass-loader#imports */
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@700&display=swap');


body {
  overflow: hidden;
  font-family: "Nunito", sans-serif;
  font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
  background-color: #F2F2F2;
  min-height: 100vh;
}

.window {
  border-radius: 20px;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.15), 0 1.5px 6px rgba(0,0,0,0.08);
  min-height: 70vh;
  min-width: 350px;
  max-width: 420px;
  margin: 48px auto 0 auto;
  padding: 32px 32px 56px 32px;
  background-color: #FFFFFF;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  position: relative;
}

form > div {
  margin-bottom: 18px;
  display: flex;
  flex-direction: column;
}

label {
  margin-bottom: 6px;
  color: #222;
  font-size: 1rem;
  font-weight: 700;
}

input[type="email"],
input[type="password"],
input[type="text"],
input[type="time"],
input[type="date"] {
  padding: 10px 14px;
  border: 1.5px solid #e0e0e0;
  border-radius: 12px;
  font-size: 1rem;
  background: #F2F2F2;
  transition: border 0.2s;
  outline: none;
}
input:focus {
  border: 1.5px solid #a0a0a0;
}

.save-button {
  background-color: #FFFFFF;
  border: 1.5px solid #F2F2F2;
  width: 100%;
  height: 44px;
  padding: 0 10px;
  border-radius: 25px;
  text-align: center;
  text-decoration: none;
  font-size: 1.1rem;
  font-weight: 700;
  cursor: pointer;
  position: absolute;
  bottom: 24px;
  left: 32px;
  right: 32px;
  box-shadow: 0 2px 8px rgba(31, 38, 135, 0.07);
  transition: background 0.2s, box-shadow 0.2s;
}
.save-button:hover {
  background: #F2F2F2;
  box-shadow: 0 4px 16px rgba(31, 38, 135, 0.12);
}

nav {
  display: flex;
  justify-content: center;
  gap: 24px;
  margin-bottom: 32px;
}

nav a {
  color: #222;
  text-decoration: none;
  font-size: 1.1rem;
  font-weight: 700;
  padding: 6px 16px;
  border-radius: 12px;
  transition: background 0.2s, color 0.2s;
}
nav a:hover {
  background: #F2F2F2;
  color: #0077ff;
}
